* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
  font-family: Poppins, sans-serif;
  font-size: 62.5%;
}

body {
  line-height: 2;
}

.main-content {
  width: 100%;
  height: 100%;
  border-top: 1.6rem solid #31493c;
  border-bottom: 1.6rem solid #31493c;
  margin: 0 0 3rem;
  position: relative;
}

img {
  width: 100%;
}

.container {
  width: 90%;
  max-width: 140rem;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .container {
    width: 100%;
    padding: 0 6vw 0 calc(6vw + 10rem);
  }
}

.btn {
  color: #31493c;
  letter-spacing: .1rem;
  background-color: #e8f1f2;
  border: none;
  border-radius: 1rem;
  padding: .8rem 2rem;
  font-family: Poppins, sans-serif;
  font-size: 1.6rem;
  font-weight: 700;
  transition: background-color .3s;
  box-shadow: .2rem .2rem .3rem #001a23;
}

@media (min-width: 768px) {
  .btn {
    padding: 1rem 3rem;
    font-size: 2rem;
  }
}

.btn:active {
  transform: scale(.95);
}

.btn:hover {
  cursor: pointer;
  color: #b3efb2;
  background-color: #001a23;
  box-shadow: .2rem .2rem .3rem #b3efb2;
}

input:not([type="submit"]) {
  width: 100%;
  background-color: #0000;
  border: none;
  border-bottom: .4rem solid #b3efb2;
  border-radius: 1rem;
  outline: none;
  margin: 2rem 0;
  padding: 1rem 0;
  font-family: Poppins, sans-serif;
  transition: border-color .4s;
}

input:not([type="submit"])::placeholder, input:not([type="submit"]):focus, input:not([type="submit"]):enabled {
  text-align: center;
  font-family: "Poppins, " sans-serif;
  color: #d8e7e9;
  color: 1.2rem;
  font-size: 1.6rem;
}

input:not([type="submit"]):invalid {
  border-color: #b00020;
}

.sk-circle {
  width: 8rem;
  height: 8rem;
  margin: 100px auto;
  position: relative;
}

.sk-circle .sk-child {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.sk-circle .sk-child:before {
  content: "";
  width: 15%;
  height: 15%;
  background-color: #b3efb2;
  border-radius: 100%;
  margin: 0 auto;
  animation: 1.2s ease-in-out infinite both sk-circleBounceDelay;
  display: block;
}

.sk-circle .sk-circle2 {
  transform: rotate(30deg);
}

.sk-circle .sk-circle3 {
  transform: rotate(60deg);
}

.sk-circle .sk-circle4 {
  transform: rotate(90deg);
}

.sk-circle .sk-circle5 {
  transform: rotate(120deg);
}

.sk-circle .sk-circle6 {
  transform: rotate(150deg);
}

.sk-circle .sk-circle7 {
  transform: rotate(180deg);
}

.sk-circle .sk-circle8 {
  transform: rotate(210deg);
}

.sk-circle .sk-circle9 {
  transform: rotate(240deg);
}

.sk-circle .sk-circle10 {
  transform: rotate(270deg);
}

.sk-circle .sk-circle11 {
  transform: rotate(300deg);
}

.sk-circle .sk-circle12 {
  transform: rotate(330deg);
}

.sk-circle .sk-circle2:before {
  animation-delay: -1.1s;
}

.sk-circle .sk-circle3:before {
  animation-delay: -1s;
}

.sk-circle .sk-circle4:before {
  animation-delay: -.9s;
}

.sk-circle .sk-circle5:before {
  animation-delay: -.8s;
}

.sk-circle .sk-circle6:before {
  animation-delay: -.7s;
}

.sk-circle .sk-circle7:before {
  animation-delay: -.6s;
}

.sk-circle .sk-circle8:before {
  animation-delay: -.5s;
}

.sk-circle .sk-circle9:before {
  animation-delay: -.4s;
}

.sk-circle .sk-circle10:before {
  animation-delay: -.3s;
}

.sk-circle .sk-circle11:before {
  animation-delay: -.2s;
}

.sk-circle .sk-circle12:before {
  animation-delay: -.1s;
}

@keyframes sk-circleBounceDelay {
  0%, 80%, 100% {
    transform: scale(0);
  }

  40% {
    transform: scale(1);
  }
}

